
.list-item {
  position: relative;
}
.list-item .list-control {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: absolute;
  border: 0.013333rem solid #dad8d7;
  border-radius: 0.053333rem;
  width: 2.133333rem;
  height: 0.4rem;
  left: 50%;
  top: 50%;
  margin-top: -0.2rem;
  margin-left: -1.066667rem;
  background-color: white;
  -webkit-box-shadow: 0 0 0.133333rem rgba(0, 0, 0, 0.1);
          box-shadow: 0 0 0.133333rem rgba(0, 0, 0, 0.1);
  overflow: hidden;
}
.list-item .list-control > span {
  width: 50%;
  height: 100%;
  text-align: center;
  line-height: 0.4rem;
  cursor: pointer;
}
.list-item .list-control > span:hover {
  color: white;
  background-color: rgba(0, 0, 0, 0.9);
}
.list-item .list-control > span:first-child {
  border-radius: 0.053333rem 0 0 0.053333rem;
}
.list-item .list-control > span:last-child {
  border-radius: 0 0.053333rem 0.053333rem 0;
}


