
@media screen and (max-width: 768px) {
h1 {
    font-size: 0.373333rem;
}
}
.context-list {
  width: 100%;
  margin-bottom: 0.4rem;
}
.context-list .list-heading {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.context-list .list-heading .title {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.context-list .list-heading .title .img {
  margin: 0 0.266667rem 0 0.666667rem;
}
.context-list .list-heading .add {
  display: none;
}
.context-list .list-heading button {
  width: 0.4rem;
  height: 0.4rem;
  font-size: 0.32rem;
  border-radius: 50%;
  background: none;
  border: 0.013333rem solid #dad8d7;
  cursor: pointer;
}
.context-list .list-heading button:hover {
  background: #f2f2f2;
}
.icon-class {
  background-color: #f6f7f7;
  border-bottom: 0.013333rem solid #e9e8e8;
}
.icon-margin-bottom {
  margin-bottom: 0;
}
.icon-margin-right {
  margin-right: 0.2rem;
}


