
.edit-image {
  position: relative;
  display: inline-block;
  z-index: 999;
}
.edit-image img {
  width: 100%;
  height: 100%;
}
.edit-image input {
  display: inline-block;
  position: absolute;
  cursor: pointer;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  opacity: 0;
}
.circle {
  border-radius: 1.413333rem;
}


